import styled from 'styled-components';

import { Outlet } from 'react-router-dom';
import { TwoColumnLayout } from '../../components/shared/twoColumnLayout';
import blogsLargeIcon from '../../assets/svgs/blogs/blogs-large.svg';
import { useAppDispatch, useAppSelector } from '../../app/hooks';
import { getBlogsFromApi, selectAllBlogs } from './blogsSlice';
import { useEffect } from 'react';

const StyledBlogs = styled.section`
  display: flex;
  flex: 1;
  flex-direction: column;
  width: 100%;
`;

const Blogs: React.FC = () => {
  const dispatch = useAppDispatch();
  const data = useAppSelector(selectAllBlogs);

  useEffect(() => {
    if (data.status === 'pending')
      dispatch(getBlogsFromApi({ searchText: '', activeTags: '' }));
  }, [data.status, dispatch]);

  return (
    <TwoColumnLayout
      leftContent={
        <div className='large-icon-top'>
          <img src={blogsLargeIcon} alt='blogs' />
        </div>
      }
    >
      <StyledBlogs>
        <Outlet />
      </StyledBlogs>
    </TwoColumnLayout>
  );
};
export default Blogs;
